@import 'variables';

.mainpg-container {
  #profile-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    // margin: 20rem;
    // font-size: 1.5rem;

    // .descriptors {
    //   display: flex;
    //   align-items: center;
    //   flex-direction: column;

    //   .description {
    //     max-width: 30rem;
    //   }
    // }
  }
}

// mobile display
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .contact-btn {
    font-size: 1.5rem;
    // position: relative;
    // top: 0.2rem;
    // right: 0.5rem;
    margin: 0.5rem 0 0 16.75rem;
    background-color: transparent;
    border: none;
    color: white;
  }

  #profile-container {
    // display: flex;
    // align-items: center;
    margin: 1.25rem;
    box-shadow: 0.5px 1px 10px black;
    font-size: 1.25rem;

    .descriptors {
      margin: 2.5rem;
      display: flex;
      align-items: center;
      flex-direction: column;

      .name {
        margin-bottom: 0;
      }

      p {
        margin: 0.5rem;
        font-size: 1rem;
      }

      .description {
        font-style: italic;
      }

      button {
        display: none;
      }
    }
  }
}

// tablet display
@media (min-width: $breakpoint-t-min) and (max-width: $breakpoint-t-max) {
  #profile-container {
    font-size: 2rem;
    margin: 3rem;
    background-size: cover;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 1rem;
    .description {
      max-width: fit-content;
    }
  }
}
