@import 'variables';

@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .navbar {
    color: white;
    ul {
      display: flex;
      justify-content: space-between;

      li {
        margin-right: 2rem;
        list-style: none;
        .link {
          text-decoration: none;
          color: white;
        }
      }
    }
  }
}
